/* Estilos para o cabeçalho da página */
header {
    height: 100px; /* Altura do cabeçalho */
    display: flex; /* Utiliza o layout de flexbox para organizar os elementos internos */
    justify-content: space-between; /* Alinha os itens internos com espaçamento uniforme entre eles */
    align-items: center; /* Alinha os itens internos verticalmente ao centro */
    background-color: black; /* Cor de fundo do cabeçalho */
}

/* Estilos para a navegação no cabeçalho */
nav {
    width: 70%; /* Largura da navegação em relação ao cabeçalho */
}

/* Estilos para a lista de navegação */
ul {
    width: 70%; /* Largura da lista de navegação em relação à navegação */
    display: flex; /* Utiliza o layout de flexbox para organizar os itens da lista */
    justify-content: space-between; /* Alinha os itens da lista com espaçamento uniforme entre eles */
}

/* Estilos para os itens da lista de navegação */
li {
    list-style: none; /* Remove os marcadores de lista */
    color: #fff; /* Cor do texto branca */
    cursor: pointer; /* Altera o cursor ao passar o mouse sobre os itens */
    font-size: 5s; /* Tamanho da fonte (há um erro aqui, deve ser um número seguido de "px", "em" ou outra unidade) */
    font-family: Verdana, Geneva, Tahoma, sans-serif; /* Define a família de fontes */
}

/* Estilos para itens da lista de navegação ao passar o mouse sobre eles */
li:hover {
    color: red; /* Muda a cor do texto para vermelho ao passar o mouse sobre os itens */
}

/* Estilos para o logo no cabeçalho */
#logo {
    width: 100px; /* Largura do logo de 100 pixels */
    height: auto; /* Altura automática para manter a proporção */
    margin-left: 100px; /* Margem à esquerda de 100 pixels para o logo */
}

/* Estilos para dispositivos com largura máxima de 768px */
@media(max-width: 768px) {
    .footer-navigation {
        display: block; /* Exibe a navegação no rodapé para dispositivos menores */
    }
    
    #logo {
        margin-left: 20px; /* Reduz a margem à esquerda para dispositivos menores */
        margin-top: 10%; /* Adiciona um espaçamento superior para o logo */
    }
}
