/* Estilo para o banner */
#banner {
    background-image: url('https://img.elo7.com.br/product/original/3B5271E/painel-2x1m-patrulha-canina-o-filme-festa-infantil.jpg');
    background-position: 50% 50%; /* Centraliza a imagem no banner */
    background-size: cover; /* Garante que a imagem cubra todo o banner */
    width: 100%; /* Largura total */
    height: 1000px; /* Altura fixa */
}

/* Estilo para o container do trailer */
#trailer-container {
    width: 100%; /* Largura total */
    height: auto; /* Altura ajustável conforme o conteúdo */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: black; /* Fundo preto */
}

/* Estilo para o conteúdo dentro do trailer-container */
.content {
    width: 90%; /* Largura de 90% do container */
    max-width: 1200px; /* Largura máxima de 1200px para evitar que o conteúdo fique muito largo */
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 30px 0; /* Margem superior e inferior de 30px */
}

/* Estilo para o vídeo do trailer */
.trailer {
    width: 100%; /* Largura total */
    margin-top: 20px; /* Espaçamento superior de 20px */
}

/* Estilo para a sinopse */
#sinopse {
    width: 100%; /* Largura total */
    max-width: 600px; /* Largura máxima de 600px para evitar que o texto fique muito largo */
    margin-top: 20px; /* Espaçamento superior de 20px */
    text-align: justify; /* Justificar o texto */
}

/* Estilo para a descrição da sinopse */
.description {
    color: #fff; /* Cor do texto branca */
    font-size: 19px; /* Tamanho da fonte de 19px */
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin-bottom: 20px; /* Margem inferior de 20px */
}

/* Estilo para o botão de comprar ingresso */
.button {
    width: 100%; /* Largura total */
    max-width: 200px; /* Largura máxima de 200px para evitar que o botão fique muito largo */
    height: 60px; /* Altura fixa de 60px */
    background-color: #d21617; /* Cor de fundo do botão */
    color: #fff; /* Cor do texto branca */
    padding: 20px 10px; /* Preenchimento interno do botão */
    border-radius: 5px; /* Bordas arredondadas */
    cursor: pointer;
    transition: .8s; /* Efeito de transição de 0.8 segundos */
    font-size: 18px; /* Tamanho da fonte de 18px */
    font-weight: bold;
    border: none;
    outline: none;
    margin-top: 25px; /* Margem superior de 25px */
}

/* Efeito de hover para o botão */
.button:hover {
    background-color: #fff; /* Cor de fundo branca durante o hover */
    color: black; /* Cor do texto preta durante o hover */
}

/* Estilo para o container do footer */
#footer {
    display: flex;
}

/* Estilos específicos para dispositivos com largura máxima de 768px */
@media screen and (max-width: 768px) {
    nav {
        display: none; /* Oculta a navegação no header */
    }

    /* Ajustes para o banner */
    #banner {
        background-position: 60%; /* Posiciona a imagem do banner */
        margin-top: 10%; /* Margem superior reduzida */
    }

    /* Ajustes para o trailer-container */
    #trailer-container {
        height: 500px; /* Altura fixa de 500px */
        display: flex;
        flex-direction: column;
    }

    /* Ajustes para o conteúdo */
    .content {
        width: 95%; /* Largura de 95% do container */
        display: flex;
        flex-direction: column;
    }

    /* Ajustes para o trailer */
    .trailer {
        width: 100%; /* Largura total */
        margin-top: 15%; /* Espaçamento superior reduzido */
        margin-left: 5%; /* Margem à esquerda */
    }

    /* Ajustes para a sinopse */
    #sinopse {
        width: 95%; /* Largura de 95% do container */
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    /* Ajustes para a descrição da sinopse */
    .description {
        width: 100%; /* Largura total */
