.actor-cards-container{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 50px;
    background-color: black;
}
.cards-content{
    width: 90%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 20px;
    column-gap: 40px;
}
.card{
    width: 100%;
    height: 600px;
    background-size: cover;
    background-position: 50% 50%;
    border-radius: 5px;
    cursor: pointer;
    transition: 1s;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 0 0 10px 10px;
    color: black;
    font-size: 17px;
    font-weight: bold;
    font-family: Verdana, Geneva, Tahoma, sans-serif
}
.card:hover{
    transform:scale(1.02);
}
.banner-1{
    background-image: url("https://cf.shopee.com.br/file/736ab8d3154423cbb1335ffeda6c7c2f");
}

.banner-2{
    background-image: url("https://memmo-live-attachments-live.imgix.net/6dfb9b77-3420-4180-b479-8ad0665205c2/6dfb9b77-3420-4180-b479-8ad0665205c2-processed.jpeg?auto=format&w=400&h=740&fit=crop");
}
.banner-3{
    background-image: url("https://i.pinimg.com/564x/c7/9e/28/c79e28ce42af8b8e96f05c9a1ebbeb5e.jpg");
}

@media screen and (max-width: 768px) {
    .cards-content {
        grid-template-columns: 1fr;
        margin-top: 20px; /* Ajuste o valor conforme necessário para posicionar os cards corretamente */
    }

    .card {
        max-height: 300px; /* Defina um tamanho máximo menor para os cards em telas menores */
    }
    .cards-content{
        grid-template-columns: 1fr; 
              
    }
}