/* Estilos para o corpo da página */
body {
  display: grid; /* Utiliza o layout de grid para organizar o conteúdo */
  grid-template-rows: 1fr auto; /* Duas linhas, uma para o conteúdo principal e outra para o rodapé */
  min-height: 100vh; /* Altura mínima da viewport */
  margin: 0; /* Remove as margens padrão do corpo da página */
}

/* Estilos para o rodapé */
.footer {
  color: #fff; /* Cor do texto branca */
  text-align: center; /* Alinhamento do texto no centro */
  padding: 20px; /* Espaçamento interno de 20 pixels */
  position: relative; /* Posição relativa */
  bottom: 0; /* Alinha o rodapé na parte inferior da viewport */
  width: 100%; /* Largura total */
}

/* Estilos para o conteúdo dentro do rodapé */
.footer-content {
  display: flex; /* Utiliza o layout de flexbox para organizar os elementos internos */
  flex-direction: column; /* Organiza os elementos em coluna */
  align-items: center; /* Alinha os itens no centro */
}

/* Estilos para elementos de texto com cor branca */
span {
  color: #fff; /* Cor do texto branca */
  font-family: Verdana, Geneva, Tahoma, sans-serif; /* Define a família de fontes */
}

/* Estilos para a navegação no rodapé */
.footer-navigation {
  display: none; /* Inicialmente oculta a navegação no rodapé */
}

/* Estilos para a lista no rodapé */
.footer-list {
  display: flex; /* Utiliza o layout de flexbox para organizar os itens da lista */
  flex-direction: column; /* Organiza os itens da lista em coluna */
  align-items: center; /* Alinha os itens no centro */
}

/* Estilos para a imagem no rodapé */
#img {
  width: 150px; /* Largura da imagem de 150 pixels */
  position: relative; /* Posição relativa */
}

/* Estilos para parágrafos no rodapé */
p {
  color: #fff; /* Cor do texto branca */
  font-family: Verdana, Geneva, Tahoma, sans-serif; /* Define a família de fontes */
  text-align: justify; /* Justifica o texto */
  margin: 5px 0; /* Margens superior e inferior de 5 pixels */
}

/* Estilos para dispositivos com largura máxima de 768px */
@media(max-width: 768px) {
  .footer-navigation {
    display: block; /* Exibe a navegação no rodapé */
    margin-left: -20px;
    
  }
  .footer {
    display:flex;
   margin-left: 50px;
   
    


  }
}


