#comentario {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

.bita, .coragem, .luluzinha {
  width: 100%;
  max-width: 600px;
  margin-bottom: 20px;
}

h1 {
  font-style: italic;
  font-weight: bold;
  margin-bottom: 10px;
  color: red;
}

p {
  font-size: 16px;
  line-height: 1.5;
  color: white;
}

@media screen and (max-width: 768px) {
  #comentario {
    width: 70%;
    padding: 10px;
  }

  .bita, .coragem, .luluzinha {
    padding: 10px;
  }
  h1{
    font-size: 25px;
  }
}
