.person{
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
.cards-content{
  width: 90%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 20px;
  column-gap: 40px;
}
.card{
  max-width: 100%;
  width: 100%;
  height: 500px;
  background-size: cover;
  display:flex;
  border-radius: 5px;
  cursor: pointer;
  transition: 1s;

  

  color: black;
  font-size: 17px;
  font-weight: bold;
  font-family: Verdana, Geneva, Tahoma, sans-serif
}
.card:hover{
  transform:scale(1.02);
}
