.container {
    max-width: 400px;
    margin: 0 auto;
    padding: 40px;
    border: 2px solid blue;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
    
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
    font-family: Verdana, Geneva, Tahoma, sans-serif;

  }
  
  .inputbox {
    position: relative;
    margin-bottom: 25px;
    
  }
  
  input,
  textarea {
    width: calc(100% - 20px);
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 8px;
    
    

  }
  
  label {
    position: absolute;
    top: -15px;
    left: 10px;
    font-size: 14px;
    background-color: #f9f9f9;
    padding: 0 5px;
    color: #888;
    transition: 0.2s;
    
    

    

  }
  
  .inputbox input:focus ~ label,
  .inputbox textarea:focus ~ label,
  .inputbox input:valid ~ label,
  .inputbox textarea:valid ~ label {
    top: -10px;
    font-size: 12px;
    color: #4caf50;
    
  }
  
  button {
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 15px 25px;
    font-size: 18px;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.3s;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  @media(max-width: 768px) {
   .container{
    width: 50%;
    height: auto;
    margin-top: 10%;


   }

  }